import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  ReferenceLine,
  Label,
  Brush,
} from "recharts"

import { useState, useRef, createRef } from "react"

import moment from "moment"

import chroma from "chroma-js"

import "moment/locale/fr"
import "moment/locale/en-gb"

export default function Chart({ water, cities }) {
  const french = navigator.language === "fr"
  if (french) {
    moment.locale("fr")
  }
  const displayEaster = document?.location?.hash !== ""
  const xAxis = "semaine"

  const waterValues = water?.map((week) =>
    Object.entries(week)
      // remove undefined values for a city this week
      .filter(([name, value]) => value !== "NA")
      // format the city name according to cities dataset
      .map(([name, value]) => {
        if (name !== "National" && name !== xAxis) {
          name = cities?.find(({ nom }) => name === nom)?.commune.toLowerCase()
        }
        return [name, value]
      })
      // re-transform as object
      .reduce(
        (accumulator, [name, value]) => ({
          ...accumulator,
          [name]:
            name === xAxis
              ? //
                moment(value, "YYYY-W")
                  .add(7, "d")
                  .format(french ? "YYYY-DD-MM" : "YYYY-MM-DD")
              : Number(value.split(",")[0]),
        }),
        {}
      )
  )

  // set national in top position of the object
  const citiesName = cities.map(({ commune }) => commune.toLowerCase())

  if (citiesName.length > 0) citiesName.unshift("National")
  // generate palet of colors
  const colors = chroma
    .cubehelix()
    .start(0)
    .rotations(0.8)
    .lightness(0.5)
    .gamma(0.8)
    .hue(4)
    .scale()
    .colors(citiesName.length)

  /*

  filtering

  */
  const [showCity, setShowCity] = useState([])
  const [tempHover, setTempHover] = useState("")

  const filterWaterValue = (weeks, filters) =>
    weeks.map((week, index) =>
      Object.entries(week)
        // .filter(([name]) => filters.includes(name) || name === xAxis)
        .map(([city, value], index, all) => {
          if (filters.includes(city) || city === xAxis) {
            return [city, value]
          } else {
            return [city, 0]
          }
        })
        .reduce(
          (accumulator, [name, value]) => ({ ...accumulator, [name]: value }),
          {}
        )
    )
  // extract max of latestweek to display in the top of graph, more the biggest known cities
  const lastestWeek = waterValues.slice(-1)?.[0]
  let max =
    lastestWeek &&
    Object.entries(lastestWeek).sort(
      ([nameA, valueA], [nameB, valueB]) => valueB - valueA
    )?.[1]?.[0]

  // initial filter is National, 3 biggest cities and the zone with the max value
  const initialFilter = [
    ...new Set(["National", "paris", "marseille", "toulouse", max]),
  ]

  // build filter initial filter or user choice
  let filter = showCity.length === 0 ? initialFilter : showCity

  let tempFilter = tempHover ? [...filter, tempHover] : filter

  const filtered = filterWaterValue(waterValues, tempFilter)

  const refs = useRef(citiesName.map((city, index) => createRef()))

  const easters = [
    {
      x: "2022-09-05",
      fr: "❝C'est un rhume❞",
      en: "❝It's mild❞",
      position: "insideBottom",
    },
    {
      x: "2022-10-17",
      fr: "❝un soubressaut qui sait❞",
      en: "❝just a bump who knows❞",
      position: "center",
    },
    {
      x: "2022-12-12",
      fr: "❝Personne n'aurait pu prévoir ça❞",
      en: "❝No one could foreseen this❞",
      position: "insideTop",
    },
    {
      x: "2023-02-13",
      fr: "❝C'est saisonnier❞",
      en: "❝It's seasonal❞",
      position: "insideBottom",
    },
    {
      x: "2023-05-01",
      fr: "❝5 mois de mémoire immunitaire, incroyable❞",
      en: "❝5 monthes of immune memory, incredible❞",
      position: "center",
    },
    {
      x: "2023-07-10",
      fr: "❝C'est terminé❞",
      en: "❝It's over❞",
      position: "insideBottom",
    },

    {
      x: "2023-12-18",
      fr: "❝Personne n'aurait pu prédire ça❞",
      en: "❝No one could have predicted this❞",
      position: "insideTop",
    },
    {
      x: "2023-09-04",
      fr: "❝Imprévisible cocktail des variants d'été❞",
      en: "❝Summer variant soup was unpredictable❞",
      position: "center",
    },
    {
      x: "2024-04-08",
      fr: "❝Immunité de masse❞",
      en: "❝Herd immunity❞",
      position: "insideBottom",
    },
    {
      x: "2024-06-24",
      fr: "❝Un variant, on l'a pas vu venir❞",
      en: "❝A new variant, we dont see it coming❞",
      position: "insideRight",
    },
  ]

  return (
    <>
      <ResponsiveContainer
        width="90%"
        height={"100%"} // will set max-height for his subwrapper
        minHeight={400}
        maxHeight={"10%"}
        style={{ resize: "both", overflow: "hidden", height: "100%" }}
      >
        <LineChart
          data={filtered}
          margin={{
            right: 30,
            left: 20,
          }}
        >
          {citiesName.map((city, index) => (
            <Line
              type="monotone"
              connectNulls
              dot={false}
              dataKey={city}
              className={!tempFilter?.includes(city) && "desactivated"}
              key={city}
              ref={refs.current[index]}
              stroke={
                tempFilter?.includes(city)
                  ? colors[index]
                  : "rgb(122, 122, 122)"
              }
              strokeWidth={tempHover && city === tempHover ? 5 : 2}
              strokeDasharray={city === "National" ? "4 4" : "0 0"}
              name={city}
              id={city}
              isAnimationActive={true}
              animationDuration={200}
            ></Line>
          ))}

          <CartesianGrid strokeDasharray="3 3" />
          <XAxis tickMargin={5} dataKey={xAxis} />
          <YAxis
            label={{
              value: french
                ? "Concentration virale cg/L"
                : "Viral concentration cg/L",
              angle: -90,
              position: "left",
              textAnchor: "middle",
            }}
          ></YAxis>
          <Brush
            travellerWidth={15}
            dataKey={(a) => {
              return a[xAxis]
            }}
          />
          <Legend
            width={97}
            align="center"
            wrapperStyle={{
              width: "98%",
            }}
            onMouseOver={(e) => {
              setTempHover(e.value)
            }}
            onMouseOut={(e) => {
              setTempHover(false)
            }}
            onClick={(e) => {
              if (filter.includes(e.value)) {
                setShowCity(filter.filter((name) => name !== e.value))
              } else {
                setShowCity([...filter, e.value])
              }
              // setTempHover(e.value)
              setTimeout(() => {
                setTempHover(false)
              }, 500)
            }}
          />
          <Tooltip
            offset={40}
            contentStyle={{
              background: "rgb(33, 37, 43)",
              textAlign: "left",
            }}
            content={({ active, payload, label }) =>
              active &&
              payload &&
              label && (
                <div className="recharts-default-tooltip">
                  <p className="recharts-tooltip-label">
                    {moment(label, french ? "YYYY-DD-MM" : "YYYY-MM-DD").format(
                      "DD MMMM YYYY"
                    )}
                  </p>
                  <ul className="recharts-tooltip-item-list">
                    {payload
                      .sort((A, B) => B.value - A.value)
                      .map(
                        (line, index) =>
                          line.value > 0 && (
                            <li key={index} className="recharts-tooltip-item">
                              <span
                                className="recharts-tooltip-item-name"
                                style={{
                                  color: line.color,
                                }}
                              >
                                {line.name}
                              </span>{" "}
                              <span className="recharts-tooltip-item-value">
                                {line.value}
                              </span>
                            </li>
                          )
                      )}
                  </ul>
                </div>
              )
            }
          />
          {displayEaster &&
            easters.map(({ x, position, fr, en }, index, all) => (
              <ReferenceLine
                x={moment(x, "YYYY-MM-DD").format(
                  french ? "YYYY-DD-MM" : "YYYY-MM-DD"
                )}
                stroke="transparent"
                label={
                  <Label
                    width={120}
                    offset={
                      (position === "insideTop" && 15) ||
                      (position === "insideBottom" && 50)
                    }
                    value={french ? fr : en}
                    position={position}
                  />
                }
              />
            ))}
        </LineChart>
      </ResponsiveContainer>
      <p className="info">
        {french
          ? "🛈 Cliquez sur la légende pour sélectioner le détail d'une ville. Ou afficher:"
          : "🛈 Click on the legend to select a city. Or directly display:"}
        <button
          className="reset"
          data={{}}
          onClick={(e) => {
            showCity.length === citiesName.length
              ? setShowCity(["National"])
              : setShowCity(citiesName)
          }}
        >
          {showCity.length === citiesName.length
            ? french
              ? "La moyenne nationale"
              : "National average"
            : french
            ? "Toutes les villes"
            : "All cities"}
        </button>{" "}
        <br />{" "}
        {french
          ? "🛈 Utilisez la goutière sous le graphique pour cibler une période dans les données."
          : "🛈 Use the gutter below the graph to target a specific timeseries"}
        <br />{" "}
      </p>
    </>
  )
}
